/* 标题 */
.pic-details-demo-title {
  border: 0;
  width: 95%;
  margin: 20px auto;
  text-align: center;
}
.pic-details-demo-title h1 {
  color: #666666;
  font-size: 26px;
}
.pic-details-demo-title p {
  color: #878787;
  font-size: 12px;
}
/* 图片 */
.pic-details-demo-image-wrapper {
  position: relative;
  list-style: none;
  width: 90vw;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}
.pic-details-demo-image-wrapper li {
  width: 220px;
  height: 76px;
  z-index: 0;
}
.pic-details-demo-image-box-shadow {
  box-shadow: 0 0 0 rgba(140, 140, 140, 0);
  height: 100%;
  -webkit-transition: box-shadow 0.3s;
  transition: box-shadow 0.3s;
}
.pic-details-demo-image-wrapper li.open .pic-details-demo-image-box-shadow {
  box-shadow: 0 2px 8px #8c8c8c;
}
.pic-details-demo-image-wrapper li a {
  width: 110px;
  z-index: 1;
  display: inline-block;
  position: absolute;
}
.pic-details-demo-image-wrapper li a,
.pic-details-demo-image-wrapper li a img {
  display: block;
}
.pic-details-demo-text-wrapper {
  width: 50%;
  background: #fff;
  padding: 10px 15px;
  height: 90vh;
  vertical-align: top;
  display: inline-block;
  position: absolute;
}
.pic-details-demo-text-wrapper h1 {
  font-size: 18px;
  margin: 5px auto;
}
.pic-details-demo-text-wrapper i {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.pic-details-demo-text-wrapper em {
  height: 2px;
  width: 60px;
  background: #FA1F4E;
  border-radius: 2px;
  display: block;
}
.pic-details-demo-text-wrapper p {
  font-size: 12px;
  margin-top: 10px;
  line-height: 16px;
}
@media screen and (max-width: 414px) {
  .pic-details-demo {
    -webkit-transform: scale(0.65) translateX(12px);
            transform: scale(0.65) translateX(12px);
    -webkit-transform-origin: left center;
            transform-origin: left center;
  }
}
@media screen and (max-width: 375px) {
  .pic-details-demo {
    -webkit-transform: scale(0.6) translateX(7px);
            transform: scale(0.6) translateX(7px);
  }
}
@media screen and (max-width: 320px) {
  .pic-details-demo {
    -webkit-transform: scale(0.5) translateX(12px);
            transform: scale(0.5) translateX(12px);
  }
}
